}

.gallery-title h2 {
  font-family:'Courier New', Courier, monospace;
  text-align: center;
  font-style: italic;
  color: #22276e;
}

.gallery {
  width: 100%;
  column-count: 3;
  column-width: 33%;
  padding: 12px 12px;
}

.gallery .pics {
  transition: all 350ms ease;
  cursor: pointer;
  margin-bottom: 12px;
}

.gallery .pics:hover {
    filter: opacity(.8);
}

.images {
    height: auto;
    width: 100%;
}

@media (max-width: 991px) {
  .gallery {
    column-count: 2;
  }
}

@media (max-width: 480px) {
  .gallery {
    column-count: 1;
