  position: relative;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.profile-top-text a {
  position: relative;
  color: #f55a3e;
  text-decoration: none;
  cursor: pointer;
}

.profile-top-text a:hover {
  color: #f55a3e;
}

.profile-top-text a::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #f55a3e;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.profile-top-text a:hover::before {
  transform: scaleX(1);
}

.profile-top {
  width: 100%;
  position: absolute;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.profile-img {
  z-index: -1;
  height: 120vh;
}

.profile-top-container {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  flex-direction: row;
  justify-content: flex-start;
  z-index: 1;
